#wrapper{
  height: 400px;
  overflow-y: scroll;
}

.product-added{
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgba(255,255,255,0.75);
  padding: 0 0 3.5%;
  width: 90%;
  margin: 0 auto 15px;
}

.product-added:last-of-type{
  margin-bottom: 25px;
}

.product-added figure{
  margin: 0;
  width: 30%;
}

.product-added figure img {
  width: 100%;
  object-fit: cover;
  max-height: 150px;
}

.information-product-added{
  text-align: left;
  width: 70%;
  overflow: hidden;
}

.information-product-added span{
  display: block;
}

.buy{
  background-color: #D6C333;
  padding: 5px 20px;
  border-radius: 10px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
  margin-top: 10px;
}

.trash{
  color: rgba(255, 50, 20, 0.85);
  font-weight: normal;
}
.trash:hover{
  border-radius: 4px;
  background: rgba(255,255,255,0.15);
}

#wrapper .trash{
  margin-left: 5%;
}

#wrapper button:hover{
  opacity: 0.6;
}

#buy-all{
  background-color: #D6C333;
  padding: 10px 30px;
  border-radius: 10px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 640px){
  .product-added{
    display: block;
  }
  .product-added figure{
    margin: 0 auto 5px;
  }
  .information-product-added{
    text-align: center;
    width: 100%;
  }
  .information-product-added span{
    margin-bottom: 5px;
  }
}

@media screen and (max-width: 640px){
  .product-added figure{
    width: 50%;
  }
}